import Icons, { IconName } from "@components/Icons";
import Action from "@components/Action";
import IconButton from "@components/IconButton";
import { toast } from "react-toastify";

export default function ActionAppHeader() {
    return (
        <Action>
            <IconButton href="/">
                <Icons iconName={IconName.Home2} />
            </IconButton>
            <IconButton href="/inbox" onClick={() => { toast(' Khng c tin nhn mi') }}>
                <Icons iconName={IconName.MessageCircleLines} />
            </IconButton>
            <IconButton href="/create-post/">
                <Icons iconName={IconName.PlusCircle} />
            </IconButton>
            <IconButton onClick={() => { toast(' Khng c thng bo mi') }}>
                <Icons iconName={IconName.Bell} />
            </IconButton>
            <IconButton onClick={()=>{toast('Hin tnh nng hon ho ny cha kh dng')}}>
                <Icons iconName={IconName.Explore} />
            </IconButton>
        </Action>
    )
}